.page-title-container {
  min-height: 43px;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.page-title-container.compact {
  padding: 0 0 0 5px;
}
.page-title-container .overlay {
  width: 100%;
  min-height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
}
.page-title-container .overlay > * {
  flex-grow: 1;
  flex-basis: 0;
}
.page-title-container .overlay > .slot {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.page-title-container .overlay > .slot :slotted(*) {
  margin-left: 12px;
  margin-top: 5px;
}
.page-title-container .overlay > .slot :slotted(*):last-child {
  margin-right: 8px;
}
.page-title-container > .title {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'SFProDisplay', sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  letter-spacing: 3.1px;
  color: #fff;
  flex: 1;
}
