.preview-container {
  background-color: rgba(240,205,205,0.07);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
  user-select: none;
}
.preview-container>.preview {
  width: 100%;
  height: 100%;
  position: absolute;
}
.preview-container>.loading {
  position: absolute;
  z-index: 100;
  border-radius: 10px;
  backdrop-filter: blur(30px) brightness(70%);
  padding: 3px 10px 6px 10px;
  color: rgba(255,255,255,0.85);
  font-size: 14px;
}
.preview-container>.error {
  color: #000;
  z-index: 100;
}
