.my-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.my-page .description {
  font-size: 14px;
  text-align: center;
  margin: 0px 0px 10px 0px;
  font-family: 'SFProDisplay', sans-serif;
}
.my-page .description.pending {
  color: #ed69ff;
  opacity: 0.8;
}
.my-page .description.rejected {
  color: #ff746b;
  opacity: 0.8;
}
.my-page .description > i {
  display: block;
  margin-top: 6px;
}
.my-page > .gallery-container {
  flex: 1;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  min-height: 0;
  margin-top: 15px;
}
.my-page > .gallery-container > .list {
  margin-top: 20px;
  flex: 1;
}
