.add-image-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: rgba(26,246,214,0.1);
  height: 100%;
}
.add-image-container[readonly] {
  opacity: 0.6;
}
.add-image-container .remove {
  background-color: rgba(0,0,0,0.7);
  position: absolute;
  z-index: 2;
  border-radius: 9999px;
  cursor: pointer;
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.add-image-container .remove>img {
  width: 20px;
  height: 20px;
}
.add-image-container .image {
  width: 100%;
  height: 100%;
  border: 1px dashed transparent;
}
.add-image-container .files {
  visibility: hidden;
  width: 0;
}
.add-image-container .text {
  width: 100%;
  height: 100%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #1af6d6;
}
.add-image-container .text:not([readonly]) {
  cursor: pointer;
}
.add-image-container .text[readonly] {
  opacity: 0.6;
}
.error {
  font-size: 12px;
  font-weight: 400;
  font-style: italic;
  text-align: right;
  padding-right: 4px;
}
