.collection-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.collection-container .description {
  font-size: 14px;
  text-align: center;
  color: #fff;
  margin: 10px 0px 20px 0px;
  font-family: 'SFProDisplay', sans-serif;
}
.collection-container .description > i {
  opacity: 0.7;
  line-height: 29px;
}
.collection-container .scrollable {
  overflow-y: overlay;
  overflow-x: hidden;
  flex: 1;
}
.collection-container .scrollable .fields {
  padding: 0 30px;
  display: flex;
}
.collection-container .scrollable .fields .col-first {
  flex-basis: 50%;
}
.collection-container .scrollable .fields .col-first > *:not(:last-child) {
  margin-bottom: 20px;
}
.collection-container .scrollable .fields .col-second {
  flex-basis: 50%;
  margin-left: 30px;
}
.collection-container .scrollable .fields .col-second > *:not(:last-child) {
  margin-bottom: 20px;
}
.collection-container .scrollable .text {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #1af6d6;
  cursor: pointer;
}
.collection-container .actions {
  display: flex;
  justify-content: center;
  margin-top: 35px;
  margin-bottom: 10px;
}
.collection-container .actions > *:not(:first-child) {
  margin-left: 30px;
}
