.nft {
  display: flex;
  flex-direction: column;
  width: 213px;
  border: none;
  background-color: rgba(240,205,205,0.05);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}
.nft .debug {
  text-align: center;
  color: #000;
  padding: 0px 5px;
}
.nft > .delete {
  position: absolute;
  left: 190px;
  top: 8px;
  width: 15px;
  cursor: pointer;
}
.nft > .info-row {
  box-sizing: border-box;
  padding: 0 12px 3px 12px;
  display: flex;
  align-items: center;
  flex-direction: row;
  overflow: hidden;
}
.nft > .info-row .title {
  font-size: 14px;
  color: #fff;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  padding-top: 14px;
  flex: 1;
}
.nft > .info-row .author {
  font-size: 12px;
  color: rgba(255,255,255,0.5);
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.nft .likes {
  position: absolute;
  bottom: 9px;
  right: 8px;
}
.nft .price-row {
  min-height: 31px;
  margin: 3px 0 7px 0;
  padding: 0 12px 0 14px;
  display: flex;
  align-items: center;
  flex-direction: row;
}
