.error-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'SFProDisplay', sans-serif;
}
.error-modal .title {
  font-family: 'SFProDisplay', sans-serif;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 20px;
  font-style: normal;
  text-align: center;
  color: #fff;
}
.error-modal .description {
  text-align: center;
  font-size: 14px;
  line-height: 21px;
  font-style: normal;
  font-weight: 400;
  margin-bottom: 15px;
}
.error-modal .content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  overflow: hidden;
}
.error-modal .content .error-text {
  font-family: 'SFProDisplay', sans-serif;
  font-weight: normal;
  color: #ff5a5a;
  overflow: hidden;
}
.error-modal .content .btn-copy {
  cursor: pointer;
  margin-left: 10px;
  flex: 1;
}
.error-modal .restart {
  color: rgba(255,255,255,0.7);
  text-align: center;
  margin: 15px 0px 25px;
}
