.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.content .title {
  font-size: 18px;
  font-weight: bold;
  color: #fff;
}
.content .data {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.content .data .icon-copy {
  cursor: pointer;
}
.content .controls {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
}
.content .controls > *:not(:first-child) {
  margin-left: 30px;
}
