.textarea-container {
  width: 100%;
  box-sizing: border-box;
}
.textarea-container .label {
  display: block;
  margin-bottom: 10px;
  color: rgba(255,255,255,0.6);
  font-family: 'SFProDisplay', sans-serif;
  font-size: 14px;
}
.textarea-container .input {
  box-sizing: border-box;
  font-family: 'SFProDisplay', sans-serif;
  background-color: rgba(255,255,255,0.08);
  border: none;
  outline-width: 0;
  font-size: 14px;
  color: #fff;
  padding: 0;
  border-radius: 10px;
  padding: 10px 12px;
  width: 100%;
  resize: none;
}
.textarea-container .input:read-only {
  background-color: rgba(255,255,255,0.03);
  color: rgba(255,255,255,0.3);
}
.textarea-container .input::placeholder {
  font-size: 14px;
  color: rgba(255,255,255,0.3);
}
.textarea-container .input:not(:read-only):focus {
  background-color: rgba(255,255,255,0.12);
  outline-width: 0;
}
.textarea-container .info {
  display: flex;
  justify-content: space-between;
  font-family: 'SFProDisplay', sans-serif;
  opacity: 0.7;
  font-style: italic;
  font-weight: 400;
  font-size: 12px;
}
.textarea-container .info.readonly {
  color: rgba(255,255,255,0.35);
}
