.approve-collection {
  background-color: rgba(255,255,255,0.05);
  border-radius: 10px;
  box-sizing: border-box;
  padding: 20px 20px 20px 70px;
  display: flex;
  flex-direction: column;
  font-size: 14px;
}
.approve-collection .row {
  display: flex;
  flex-direction: row;
}
.approve-collection .row > .type {
  width: 100px;
}
.approve-collection .row > .type > div {
  color: #8da1ad;
  user-select: none;
  display: inline-block;
  width: 100px;
}
.approve-collection .row > .type > div:not(:last-child) {
  margin-bottom: 12px;
}
.approve-collection .row > .info {
  display: block;
  width: 100%;
  min-width: 0;
}
.approve-collection .row > .info > div:not(:last-child) {
  margin-bottom: 12px;
}
.approve-collection .row > .info > div.link {
  cursor: pointer;
  color: #00f6d2;
}
.approve-collection .row > .info > div.link.clamp {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.approve-collection .row > .info > div.link:hover {
  text-decoration: underline;
}
.approve-collection .info-error {
  flex: 1;
}
