.button {
  font-family: 'SFProDisplay', sans-serif;
  white-space: nowrap;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  border: none;
  cursor: pointer;
  background-color: rgba(255,255,255,0.1);
  user-select: none;
  position: relative;
}
.button:not(.disabled):hover {
  box-shadow: 0 0 8px #fff;
}
.button.disabled {
  opacity: 0.6;
  cursor: auto;
}
.button.disabled :slotted(img) {
  opacity: 0.35;
}
.button:focus {
  outline: none;
}
.button.reverse {
  flex-direction: row-reverse;
}
.button.transparent {
  border-radius: 0;
}
.button.transparent:not(.disabled):hover {
  box-shadow: none;
}
.button.no-hover:hover {
  box-shadow: none;
}
.button > span {
  padding-bottom: 1px;
}
.button > .tooltip {
  box-sizing: border-box;
  visibility: hidden;
  backdrop-filter: blur(10px);
  color: #fff;
  border-radius: 6px;
  padding: 11px 11px;
  position: fixed;
  z-index: 1;
  font-family: 'SFProDisplay', sans-serif;
  font-size: 14px;
  opacity: 0.85;
}
.button:hover .tooltip {
  visibility: visible;
}
