.collection-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.collection-container > .debug {
  width: 100%;
  margin-bottom: 10px;
  text-align: center;
}
.collection-container > .list {
  margin-top: 20px;
  flex: 1;
}
.collection-container > .block {
  display: flex;
  flex-direction: column;
  padding: 20px 20px 25px 20px;
  background-color: rgba(255,255,255,0.05);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 15px;
  color: #8897a8;
  font-size: 14px;
}
.collection-container > .block .desc-short {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.collection-container > .block .btn-right {
  margin-left: auto;
}
.collection-container > .block .btn-center {
  margin-left: auto;
  margin-right: auto;
}
.collection-container > .block-long {
  padding-bottom: 10px;
}
.collection-container .info-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  padding: 12px 12px 12px 12px;
  box-sizing: border-box;
  z-index: 100;
}
.collection-container .info-container > .left {
  display: flex;
  flex-direction: row;
  border-radius: 10px;
  backdrop-filter: blur(30px) brightness(40%);
  margin-right: 20px;
  padding: 8px 18px 9px 15px;
  box-sizing: border-box;
}
.collection-container .info-container > .left > .avatar {
  margin-right: 16px;
  min-width: 72px;
  min-height: 72px;
  width: 72px;
  height: 72px;
}
.collection-container .info-container > .left > .info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.collection-container .info-container > .left > .info > .name {
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 4px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.collection-container .info-container .right .social {
  height: 16px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 20px;
}
.collection-container .info-container .right .social > * {
  backdrop-filter: blur(30px) brightness(40%);
  display: flex;
  border-radius: 5px;
  padding: 5px 5px;
}
.collection-container .info-container .right .social > * > *:not(:first-child) {
  margin-left: 6px;
}
.collection-container .info-container .right .stats {
  height: 89px;
  box-sizing: border-box;
  padding: 16px 0;
  border-radius: 10px;
  backdrop-filter: blur(30px) brightness(40%);
  display: flex;
  flex-direction: row;
}
.collection-container .info-container .right .stats > div {
  min-width: 110px;
  box-sizing: border-box;
  padding: 0px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.collection-container .info-container .right .stats > div > div {
  display: flex;
  margin-bottom: 4px;
}
.collection-container .info-container .right .stats > div > div > img {
  margin-right: 8px;
}
.collection-container .info-container .right .stats > div > span:not(.value) {
  font-size: 14px;
}
.collection-container .info-container .right .stats > div .value {
  display: flex;
  font-size: 16px;
}
.collection-container .info-container .right .stats > *:not(:last-child) {
  border-right: 1px solid rgba(255,255,255,0.1);
}
