.tabs-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  user-select: none;
}
.tabs-container .slot {
  margin-left: auto;
  display: flex;
  align-items: center;
}
.tabs-container .slot :slotted(*) {
  margin-left: 12px;
  margin-top: 5px;
}
.tabs-container .slot :slotted(*):last-child {
  margin-right: 8px;
}
.tabs-container .tab-item {
  color: rgba(255,255,255,0.3);
  font-size: 12px;
  font-weight: bold;
  cursor: pointer;
  white-space: nowrap;
}
.tabs-container .tab-item .title {
  padding: 4px 16px;
  text-transform: uppercase;
}
.tabs-container .tab-item .title.compact {
  padding: 4px 8px;
}
.tabs-container .tab-active {
  color: #fff;
}
.tabs-container .tab-active .bottom-line {
  height: 2px;
  width: 100%;
  box-shadow: 0 0 5px 0 rgba(0,246,210,0.7);
  background-color: #00f6d2;
}
