.price-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
}
.price-container .dots {
  margin-left: auto;
  cursor: pointer;
  margin-right: -4px;
}
.price-container .button {
  margin-left: auto;
}
.not-for-sale {
  margin-left: auto;
  opacity: 0.5;
  font-size: 14px;
  font-style: italic;
  color: #fff;
}
