.switch-container {
  display: flex;
  align-items: flex-end;
}
.switch-container .switch {
  position: relative;
  display: inline-block;
  width: 29px;
  height: 19px;
}
.switch-container .switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255,255,255,0.05);
  transition: 0.4s;
  border: 1px solid #b4bec8;
}
.switch-container .switch .slider::before {
  position: absolute;
  content: "";
  height: 17px;
  width: 16px;
  left: 0px;
  bottom: 0px;
  background-color: #b4bec8;
  transition: 0.4s;
}
.switch-container .switch .round {
  border-radius: 34px;
}
.switch-container .switch .round:before {
  border-radius: 50%;
}
.switch-container .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch-container .switch input:checked + .slider {
  background-color: rgba(26,246,214,0.35);
}
.switch-container .switch input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}
.switch-container .switch input:checked + .slider:before {
  transform: translateX(11px);
}
.switch-container .switch_text {
  margin-left: 10px;
  font-size: 14px;
  line-height: 17px;
  color: rgba(255,255,255,0.7);
}
