.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.content .title {
  font-size: 18px;
  font-weight: bold;
  color: #fff;
}
.content .message {
  margin: 30px 0px 25px 0px;
  font-size: 14px;
}
.content .input {
  width: 570px;
}
.content .controls {
  margin-top: 35px;
  display: flex;
  flex-direction: row;
}
.content .controls > *:not(:first-child) {
  margin-left: 30px;
}
