.admin-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.admin-page > .gallery-container {
  flex: 1;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  min-height: 0;
  margin-top: 15px;
}
.admin-page > .gallery-container > .list {
  margin-top: 20px;
  flex: 1;
}
.admin-page > .gallery-container > .buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 20px 0px;
}
.admin-page > .gallery-container > .buttons > * {
  margin-right: 15px;
}
