.collection {
  width: 442px;
  border: none;
  background-color: rgba(240,205,205,0.05);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}
.collection .debug {
  text-align: center;
  color: #000;
  padding: 0px 5px;
}
.collection > .info-row {
  display: flex;
  flex-direction: row;
  padding: 10px 16px 10px 20px;
}
.collection > .info-row > .avatar {
  margin-right: 18px;
}
.collection > .info-row > .text {
  flex: 1;
}
.collection > .info-row > .text > .label {
  font-weight: 700;
  font-size: 16px;
  height: 22px;
}
.collection > .info-row > .text > .author {
  font-size: 14px;
}
.collection > .info-row > .text > .description {
  font-size: 12px;
  color: rgba(255,255,255,0.5);
  margin-top: 6px;
  height: 32px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.collection > .info-row > .text > .line {
  height: 1px;
  opacity: 0.1;
  background-color: #fff;
  border: none;
}
.collection > .info-row > .text > .items-info {
  display: flex;
  font-size: 12px;
  margin-top: 10px;
}
.collection > .info-row > .text > .items-info .count {
  display: flex;
  flex-direction: column;
  flex-basis: 20%;
}
.collection > .info-row > .text > .items-info .count > .text {
  font-weight: bold;
  color: #fff;
}
.collection > .info-row > .text > .items-info .icon_styles {
  height: 18px;
}
