.approve-artist {
  background-color: rgba(255,255,255,0.05);
  border-radius: 10px;
  box-sizing: border-box;
  padding: 20px 20px 4px 70px;
  display: flex;
  flex-direction: column;
  font-size: 14px;
}
.approve-artist .row {
  display: flex;
  flex-direction: row;
}
.approve-artist .row > .type {
  width: 100px;
}
.approve-artist .row > .type > div {
  color: #8da1ad;
  user-select: none;
  display: inline-block;
  width: 100px;
}
.approve-artist .row > .type > div:not(:last-child) {
  margin-bottom: 12px;
}
.approve-artist .row > .info {
  display: block;
  min-width: 0;
  flex: 1;
}
.approve-artist .row > .info > div:not(:last-child) {
  margin-bottom: 12px;
}
.approve-artist .row > .info > div.link {
  cursor: pointer;
  color: #00f6d2;
}
.approve-artist .row > .info > div.link.clamp {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.approve-artist .row > .info > div.link:hover {
  text-decoration: underline;
}
.approve-artist .row > .info > .images {
  float: right;
  margin: 0px 0px 0px 15px !important;
  height: 220px;
  position: relative;
}
.approve-artist .row > .info > .images .ellipse {
  position: absolute;
  left: 164px;
  top: 81px;
  border-radius: 74px;
  width: 132px;
  height: 132px;
  background-color: #ffa500;
  display: flex;
  justify-content: center;
  align-items: center;
}
.approve-artist .info-error {
  flex: 1;
}
