.balance-container {
  margin-bottom: 10px;
  margin-top: 20px;
}
.balance-container:not(.compact) {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
}
.balance-container.compact {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 20px;
}
.balance-container .balance {
  background-color: rgba(255,255,255,0.1);
  display: grid;
  grid-template-columns: auto auto;
  border-radius: 10px;
  padding: 20px;
}
.balance-container .balance .description {
  font-size: 12px;
  color: rgba(255,255,255,0.5);
  margin-bottom: 7px;
}
.balance-container .balance .withdraw {
  align-self: end;
  display: flex;
  justify-content: flex-end;
  padding-bottom: 10px;
}
