.popup-menu {
  border-radius: 10px;
  z-index: 999;
  position: fixed;
  outline: none;
  padding: 10px 20px;
}
.popup-menu :slotted(.item) {
  color: #fff;
  padding: 6px 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  line-height: normal !important;
  white-space: nowrap;
  user-select: none;
}
.popup-menu :slotted(.item):hover {
  color: #00f6d2;
}
.popup-menu :slotted(.item)>img {
  margin-right: 10px;
}
