.edit-artist-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.edit-artist-container .description {
  font-size: 14px;
  text-align: center;
  color: #fff;
  margin: 10px 0px 30px 0px;
  font-family: 'SFProDisplay', sans-serif;
}
.edit-artist-container .description > i {
  opacity: 0.7;
  display: block;
  margin-top: 6px;
}
.edit-artist-container .intx_text {
  font-size: 14px;
  text-align: center;
  color: #fff;
  margin: 30px 0px 0px 0px;
  font-family: 'SFProDisplay', sans-serif;
}
.edit-artist-container .scrollable {
  overflow-y: overlay;
  overflow-x: hidden;
  flex: 1;
}
.edit-artist-container .scrollable .fields {
  padding: 0 30px;
  display: flex;
}
.edit-artist-container .scrollable .fields .col-first {
  flex-basis: 50%;
}
.edit-artist-container .scrollable .fields .col-first > *:not(:last-child) {
  margin-bottom: 20px;
}
.edit-artist-container .scrollable .fields .col-second {
  flex-basis: 50%;
  padding-left: 30px;
  height: 420px;
}
.edit-artist-container .scrollable .fields .col-second > *:not(:last-child) {
  margin-bottom: 20px;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container {
  position: relative;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .ellipse {
  border-radius: 74px;
  width: 150px;
  height: 150px;
  position: relative;
  left: 50%;
  transform: translate(-50%, -30%);
  display: flex;
  justify-content: center;
  align-items: center;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .ellipse .avatar {
  height: 136px;
  width: 136px;
  background-color: rgba(26,246,214,0.1);
  border-radius: 68px;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .ellipse .avatar .text {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #1af6d6;
  cursor: pointer;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .ellipse .avatar .text[readonly] {
  opacity: 0.6;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .ellipse .avatar[readonly] {
  opacity: 0.6;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .ellipse .avatar .remove {
  background-color: rgba(0,0,0,0.7);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  border-radius: 9999px;
  padding: 7px 7px 3px 7px;
  cursor: pointer;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .ellipse .avatar .files {
  visibility: hidden;
  width: 0;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .ellipse .avatar .image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 9999px;
  border: 1px dashed transparent;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .error_msg {
  text-align: center;
  margin-top: -42px;
  margin-bottom: 26px;
}
.edit-artist-container .scrollable .fields .col-second .uploads-container .error_msg > p {
  font-style: italic;
  font-size: 12px;
  font-weight: 400;
  margin: 0;
}
.edit-artist-container .scrollable .actions {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 10px;
}
.edit-artist-container .scrollable .actions > *:not(:first-child) {
  margin-left: 30px;
}
