.approve-nft {
  background-color: rgba(255,255,255,0.05);
  border-radius: 10px;
  box-sizing: border-box;
  padding: 20px 20px 20px 70px;
  display: flex;
  flex-direction: row;
  font-size: 14px;
}
.approve-nft > .info-error {
  flex: 1;
}
.approve-nft > .info {
  flex: 1;
  display: grid;
  margin-right: 20px;
  grid-template-columns: 100px auto;
  align-content: start;
}
.approve-nft > .info > .type {
  color: #8da1ad;
  margin-bottom: 12px;
  user-select: none;
}
.approve-nft > .info > .value {
  color: #fff;
}
