.nft-page-container {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}
.nft-page-container.compact {
  padding: 0px 5px;
}
.nft-page-container .content-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 0;
  flex: 1;
/*
      *  TABLE HEAD
      */
/*
      *  TABLE BODY
      */
}
.nft-page-container .content-wrapper .debug {
  text-align: center;
  color: #000;
  padding: 0px 10px;
}
.nft-page-container .content-wrapper .table-title {
  box-sizing: border-box;
  padding: 30px 0 20px 0;
  color: rgba(255,255,255,0.5);
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 3.1px;
  user-select: none;
}
.nft-page-container .content-wrapper .table-head {
  border-radius: 10px 10px 0 0;
  background-color: rgba(255,255,255,0.08);
  font-weight: 700;
  font-size: 14px;
  color: rgba(255,255,255,0.5);
  display: flex;
}
.nft-page-container .content-wrapper .table-head > div {
  padding: 9px 5px 12px 5px;
  text-align: center;
  box-sizing: border-box;
}
.nft-page-container .content-wrapper .table-head > div.sorted {
  color: #fff;
  white-space: nowrap;
}
.nft-page-container .content-wrapper .table-head > div.sorted .arrow {
  width: 8px;
  height: 8px;
  margin-left: 3px;
}
.nft-page-container .content-wrapper .table-head > div:not(:first-child) {
  flex: 1;
}
.nft-page-container .content-wrapper .table-head.compact > div:first-child {
  width: 90px;
}
.nft-page-container .content-wrapper .table-head:not(.compact) > div:first-child {
  width: 130px;
}
.nft-page-container .content-wrapper .table-body-loading {
  padding: 35px 25px 0 25px;
  text-align: center;
  color: rgba(255,255,255,0.5);
}
.nft-page-container .content-wrapper .table-body-empty {
  padding: 35px 25px 0 25px;
  text-align: center;
  color: rgba(255,255,255,0.5);
}
.nft-page-container .content-wrapper .table-body-empty img {
  padding-bottom: 10px;
}
.nft-page-container .content-wrapper .table-body {
  display: block;
  overflow-y: overlay;
}
.nft-page-container .content-wrapper .table-body .row {
  display: flex;
  padding: 12px 0 11px 0;
}
.nft-page-container .content-wrapper .table-body .row > div {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 5px;
  box-sizing: border-box;
}
.nft-page-container .content-wrapper .table-body .row > div:first-child {
  padding-left: 9px;
}
.nft-page-container .content-wrapper .table-body .row > div:last-child {
  padding-right: 9px;
}
.nft-page-container .content-wrapper .table-body .row > div:not(:first-child) {
  flex: 1;
}
.nft-page-container .content-wrapper .table-body .row.compact > div:first-child {
  width: 90px;
}
.nft-page-container .content-wrapper .table-body .row:not(.compact) > div:first-child {
  width: 130px;
}
.nft-page-container .content-wrapper .table-body .row .curr {
  padding: 0 0 3px 10px;
  line-height: 17px;
  box-sizing: border-box;
}
.nft-page-container .content-wrapper .table-body .row .text {
  padding-bottom: 3px;
}
.nft-page-container .content-wrapper .table-body > div:nth-child(odd) {
  background: rgba(255,255,255,0.04);
}
.nft-page-container .content-wrapper .table-body > div:nth-child(even) {
  background: rgba(255,255,255,0.02);
}
.nft-page-container .content-wrapper .details-row {
  display: flex;
  flex-direction: row;
  margin-top: 5px;
}
.nft-page-container .content-wrapper .details-row .separator {
  height: 1px;
  margin: 20px 0 15px 0;
  opacity: 0.2;
  border-top: solid 1px #fff;
}
.nft-page-container .content-wrapper .details-row .nft-container {
  width: 360px;
  background-color: rgba(255,255,255,0.05);
  border-radius: 10px;
  min-height: 360px;
  margin-right: 10px;
  overflow: hidden;
}
.nft-page-container .content-wrapper .details-row .nft-container .likes {
  position: absolute;
  bottom: 9px;
  right: 9px;
}
.nft-page-container .content-wrapper .details-row .info-container {
  flex: 1;
  box-sizing: border-box;
}
.nft-page-container .content-wrapper .details-row .info-container:not(:first-child) {
  padding-left: 10px;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box {
  background-color: rgba(255,255,255,0.05);
  border-radius: 10px;
  height: 100%;
  box-sizing: border-box;
  padding: 20px 27px;
  display: flex;
  flex-direction: column;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box > .title-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box > .title-row > .title {
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  flex: 1;
  padding-right: 15px;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box > .title-row > button:not(:last-child) {
  margin-right: 15px;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box > .description {
  color: #fff;
  margin-top: 20px;
  font-size: 16px;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box .bottom {
  margin-top: auto;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box .bottom > .author {
  font-size: 16px;
  color: #fff;
  display: flex;
  flex-direction: row;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box .bottom > .author > span {
  color: #00f6d2;
  margin-left: 4px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box .bottom > .collection {
  font-size: 16px;
  color: #fff;
  margin-top: 6px;
  display: flex;
  flex-direction: row;
}
.nft-page-container .content-wrapper .details-row .info-container .info-box .bottom > .collection > span {
  color: #00f6d2;
  margin-left: 4px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
