.new-box {
  width: 213px;
  height: 309px;
  border-radius: 10px;
  border: 1px dashed rgba(26,246,214,0.5);
  background-color: rgba(26,246,214,0.1);
  cursor: pointer;
  color: rgba(26,246,214,0.7);
  font-size: 14px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
