.input-container {
  box-sizing: border-box;
}
.input-container .label {
  display: block;
  margin-bottom: 10px;
  color: rgba(255,255,255,0.6);
  font-family: 'SFProDisplay', sans-serif;
  font-size: 14px;
}
.input-container .label.err {
  color: rgba(255,98,92,0.7);
}
.input-container .input {
  display: flex;
  background-color: rgba(255,255,255,0.08);
  border: none;
  border-radius: 10px;
  align-items: center;
  position: relative;
}
.input-container .input > img {
  position: absolute;
  padding: 15px 15px;
}
.input-container .input.readonly {
  background-color: rgba(255,255,255,0.03);
}
.input-container .input.err {
  background-color: rgba(255,98,92,0.07);
}
.input-container .input.err:focus-within {
  background-color: rgba(255,98,92,0.12);
}
.input-container .input:not(.err):not(.readonly):focus-within {
  background-color: rgba(255,255,255,0.12);
}
.input-container .input > input {
  font-family: 'SFProDisplay', sans-serif;
  background-color: transparent;
  border: none;
  border-radius: 10px;
  outline-width: 0;
  font-size: 14px;
  color: rgba(255,255,255,0.8);
  padding: 10px 8px;
  width: 100%;
}
.input-container .input > input:read-only {
  color: rgba(255,255,255,0.3);
}
.input-container .input > input.err {
  color: #ff625c;
}
.input-container .input > input:not(.err)::placeholder {
  font-size: 14px;
  color: rgba(255,255,255,0.3);
}
.input-container .input > input.err::placeholder {
  font-size: 14px;
  color: rgba(255,98,92,0.4);
}
.input-container .chars-err {
  color: rgba(255,98,92,0.7);
}
