.paginator {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 2px;
}
.paginator > .prev,
.paginator > .next {
  width: 68px !important;
  height: 22px;
  border-radius: 2px;
  background-color: rgba(255,255,255,0.1);
  color: rgba(255,255,255,0.5);
  font-size: 14px;
  text-align: center;
  user-select: none;
}
.paginator > .prev[disabled="true"],
.paginator > .next[disabled="true"] {
  color: rgba(255,255,255,0.15);
}
.paginator > .prev:hover:not([disabled="true"]),
.paginator > .next:hover:not([disabled="true"]) {
  color: rgba(255,255,255,0.9);
  cursor: pointer;
}
.paginator > .prev {
  margin: 0 13px 0 0;
}
.paginator > .next {
  margin: 0 0 0 16px;
}
.paginator > .page {
  margin: 3px 7px;
  font-size: 14px;
  color: rgba(255,255,255,0.5);
  cursor: pointer;
  user-select: none;
}
.paginator > .page[selected="true"] {
  cursor: auto;
  color: #00f6d2;
}
.paginator > .page:hover:not([selected="true"]) {
  color: rgba(255,255,255,0.9);
  cursor: pointer;
}
