.form-container {
  box-sizing: border-box;
}
.form-container .label {
  display: block;
  margin-bottom: 10px;
  color: rgba(255,255,255,0.6);
  font-family: 'SFProDisplay', sans-serif;
  font-size: 14px;
}
.form-container .label.error {
  color: rgba(255,98,92,0.7);
}
.custom-select {
  position: relative;
  display: flex;
  outline: none;
  background-color: rgba(255,255,255,0.08);
  border-radius: 10px;
  cursor: pointer;
}
.custom-select .selected {
  color: #fff;
  width: 100%;
  user-select: none;
  font-size: 14px;
  font-weight: normal;
  letter-spacing: 0.47px;
  padding: 12px 15px;
}
.custom-select .selected.open {
  border: none;
  border-radius: 10px;
}
.custom-select .selected.focus {
  background-color: rgba(255,255,255,0.12);
}
.custom-select .arrow {
  width: 9px;
  height: 5px;
  position: absolute;
  right: 20px;
  top: 50%;
}
.custom-select .items {
  background-color: #323232;
  color: #fff;
  position: absolute;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  right: 0;
  z-index: 1;
  margin-top: 47px;
  width: 100%;
  max-height: 250px;
  overflow-y: scroll;
}
.custom-select .items div {
  color: #fff;
  padding: 15px 0px 15px 15px;
  white-space: nowrap;
  line-height: 1;
  cursor: pointer;
  user-select: none;
}
.custom-select .items div:hover {
  color: #00f6d2;
}
.custom-select .items .highlight {
  font-weight: bold;
  color: #00f6d2;
}
.readonly {
  background: rgba(255,255,255,0.03);
  cursor: initial;
}
