.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.content > .title {
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  margin-bottom: 30px;
}
.content .fee {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  align-self: flex-start;
}
.content .fee > .title {
  opacity: 0.5;
  font-size: 12px;
  color: #fff;
}
.content .fee .value {
  font-size: 14px;
  font-weight: 500;
  color: #0bccf7;
  margin-left: 10px;
}
.content .info {
  margin-top: 20px;
  opacity: 0.7;
  font-size: 14px;
  font-style: italic;
  color: #fff;
}
.content .controls {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
}
.content .controls > *:not(:first-child) {
  margin-left: 30px;
}
.content .disabled {
  opacity: 0.3;
  cursor: auto !important;
}
