.list-container {
  display: flex;
  flex-direction: column;
  min-height: 0;
}
.list-container > .list-wrap {
  overflow-y: auto;
  overflow-x: hidden;
  justify-content: center;
}
.list-container > .list-wrap > .list {
  display: flex;
  flex-wrap: wrap;
  margin: -8px -8px 0 -8px;
}
.list-container > .list-wrap > .list > * {
  margin: 8px;
}
.list-container > .list-wrap-compact {
  overflow-y: overlay;
  overflow-x: hidden;
}
.list-container > .list-wrap-compact > .list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -8px -8px 0 -8px;
}
.list-container > .list-wrap-compact > .list > * {
  margin: 8px;
}
.list-container .empty {
  margin: 100px auto 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  user-select: none;
}
.list-container .empty > .text {
  margin-top: 30px;
  opacity: 0.5;
  font-size: 14px;
  line-height: 1.43;
  text-align: center;
  color: #fff;
}
