.admin-container .rewards-row {
  display: flex;
  margin-bottom: 20px;
}
.admin-container .moderators-list {
  border-top: 1px solid rgba(225,255,255,0.6);
  padding-top: 10px;
}
.admin-container .moderators-list table {
  margin-top: 10px;
  border-collapse: collapse;
  border: 1px solid rgba(225,255,255,0.6) !important;
  margin-bottom: 15px;
}
.admin-container .moderators-list table thead th {
  padding-bottom: 5px;
  border-bottom: 1px solid rgba(225,255,255,0.6) !important;
}
.admin-container .moderators-list table td {
  padding: 6px 10px;
}
.admin-container .moderators-list table tr:last-child td {
  padding-bottom: 9px;
}
.admin-container .moderators-list .link {
  color: #00f6d2;
  cursor: pointer;
}
