.nft-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.nft-container .description {
  font-size: 14px;
  text-align: center;
  color: #fff;
  margin: 10px 0px 20px 0px;
  font-family: 'SFProDisplay', sans-serif;
}
.nft-container .description > i {
  opacity: 0.7;
  display: block;
  margin-top: 6px;
}
.nft-container .scrollable {
  overflow-y: overlay;
  overflow-x: hidden;
  flex: 1;
}
.nft-container .scrollable .fields {
  padding: 0px 30px 0px 30px;
  display: flex;
}
.nft-container .scrollable .fields .col-first {
  flex: 1;
}
.nft-container .scrollable .fields .col-first > *:not(:last-child) {
  margin-bottom: 20px;
}
.nft-container .scrollable .fields .col-second {
  margin-left: 30px;
  margin-top: 30px;
}
.nft-container .actions {
  display: flex;
  justify-content: center;
  margin-top: 25px;
  margin-bottom: 10px;
}
.nft-container .actions > *:not(:first-child) {
  margin-left: 30px;
}
