.app-loading {
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding-bottom: 40px;
  font-size: 14px;
  user-select: none;
}
.app-loading .title {
  font-size: 17px;
  font-weight: 400;
  user-select: none;
  margin: 20px 0px 3px 0px;
}
